<template>
  <div>
    <div class="dis-flex align-item-center yak-adopt-item-box" v-for="item of 1" :key="item">
      <van-image
        src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1575786123,3276351449&fm=26&gp=0.jpg"
        fit="cover"
        @click="$emit('detail', {})"
        class="yak-image"
      />
      <div class="flex-1 font-9" style="padding: 0.786rem 0 0.429rem;">
        <div class="font-12">
          旺财
          <span class="icon-cow" style="margin-left: 0.5rem;"></span>
        </div>
        <div class="color-656565 mg-top-10">耳标号：001</div>
        <div class="mg-top-10">
          <span class="badge">4岁</span>
        </div>
        <div class="color-F7CA3B mg-top-10">
          <img
            src="../../assets/images/yak-adopt/position-yellow.png"
            class="icon-img"
            style="width: 0.857rem;height: 0.929rem;margin-right: 0.429rem;"
          />
          小金县崇德牧场
        </div>
      </div>

      <button @click="onClick" class="adopt-bt button">送礼</button>
      <van-dialog
        v-model="show"
        message="当前牦牛还未屠宰，需要屠宰后才能发起
送礼操作，是否立即屠宰？
"
        show-cancel-button
        show-confirm-button
        :beforeClose="chargeBtn"
      ></van-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "giftgiving",

  data() {
    return {
      show: false
    };
  },

  props: {},
  methods: {
    onClick() {
      this.show = true;
    },
    chargeBtn(action, done) {
      //确认or取消

      if (action === "cancel") {
        //取消按钮
        done();
      } else if (action === "confirm") {
        //确定按钮
        //向后端传值并关闭dialog弹出框
        this.show = false;
        this.$router.push({
          path: "/personal/gift-detail"
        });
      }
      done();
    }
  }
};
</script>

<style scoped lang="less">
.badge {
  color: #989898;
  border-color: #979797;
  padding: 0 0.714rem;
}

/*牦牛认养Item 容器*/
.yak-adopt-item-box {
  padding: 1.429rem 2.571rem 1.071rem 0.714rem;
  border-bottom: 1px solid #f2f2f2;

  /*牦牛图片*/

  .yak-image {
    width: 9.286rem;
    height: 7.857rem;
    margin-right: 2.071rem;
  }

  /*认养按钮*/

  .adopt-bt {
    font-size: 1.071rem;
    font-weight: bold;
    color: #3399ff;
  }
}
</style>
